@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
.navbar{
  padding: 0px !important;
  background-color: var(--secondary-colour) !important;
  margin-bottom: 20px;
}

.navbar-center{
  position: absolute;
  left: 50%;
  transform: translatex(-50%)
}

.logo {
  width: 50px;
  height: 50px;
  margin: 10px;
}

.logo:hover {
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.nav-item {
  font-size: 20px;
  margin-left: 20px;
}

.nav-link {
  color: var(--tertiary-colour) !important;
}
.nav-link:hover {
  cursor: pointer;
}

.nav-link.active{
  /* text-decoration: underline !important; */
  color: var(--primary-colour) !important;
}
#home {
  min-height: 100vh;
  padding-top: 150px;
}

.home-content {
  margin-top: 7em;
}


.profile-pic {
  border-radius: 50%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.intro {
  font-size: 3em;
  color: var(--primary-colour);
}
.intro span{
  color: var(--tertiary-colour);
}

.home-bio {
  font-size: 4em;
  text-align: center;
  margin-bottom: 15px;
  color: var(--tertiary-colour);
}

  
@media(max-width: 600px) {
  #home {
    
  background-image: url(/static/media/background2.878889d6.jpg);
  background-size: cover;
  }
}
#about{
  min-height: 100vh;
  padding-top: 6rem;
  padding-bottom: 3rem;
  background-color: var(--tertiary-colour);
}


.profile-bio {
  font-size: 24px;
  text-align: justify;
  margin-bottom: 15px;
  color: var(--secondary-colour);
  
}

.profile-bio a {
  color: #E7F3F9 !important;
}

.twoColumns{
  -webkit-column-gap: 20px;
     -moz-column-gap: 20px;
          grid-column-gap: 20px;
          column-gap: 20px;
  -webkit-column-count: 2;
          column-count: 2;
}

@media (max-width: 900px) {
  .twoColumns {
    -webkit-column-count: 1;
            column-count: 1;

  }
}
.about-row {
  margin-top: 60px;
}
.about-btn {
  width: 100%;
  border-radius: 0 !important;
  background-color: #839fb1 !important;
  color: #E7F3F9  !important;
  border: none !important;
}

.about-btn:hover {
  background-color: #576b77 !important;
}
.maintitle h1 {
  font-size: 30px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;

}

.primary-hr {
  border-top: 3px solid var(--tertiary-colour) !important;
  width: 50px;
}

.primary-h1 {
  color : var(--primary-colour);
}

.secondary-hr {
  border-top: 3px solid var(--primary-colour) !important;
  width: 50px;
}

.secondary-h1 {
  color : var(--secondary-colour);
}


#skills{
  min-height: 100vh;
  padding-top: 6rem;
  padding-bottom: 3rem;
  background-color: var(--secondary-colour);
}


.skill-text {
  font-size: 32px;
  text-align: justify;
  margin-bottom: 15px;
  color: var(--primary-colour);
  margin-top: 4rem;
}

.skill-text span{
  font-family: 'consolas';
  margin-bottom: 15px;
  color: var(--tertiary-colour);
}

.skill-icon {
  width: 120px;
  height: 120px;
  margin: 3px;
  text-align: center;
  padding: 5px;
  line-height: 120px;
  
}

.skill-icon img {
  width: 100%;
}

.skill-box {
  justify-content: center;
}

.skill-container {
  margin-top: 4rem;
  padding: 15px;
  background-color: var(--tertiary-colour);
}



.card-img-overlay {
  opacity: 0;
  transition: .5s ease;
  background-color: var(--tertiary-colour);
  text-align: center;
  border:none !important;
  border-radius: 0 !important;
}

.card {
  max-width: 450px;
  background-color: var(--tertiary-colour) !important;
  border:none !important;
  border-radius: 0 !important;
}

.btn-project{
  position: absolute;
  left: 50%;
  bottom: 20%;
  transform: translateX(-50%);
}
.card:hover .card-img-overlay{
  opacity: 1;
}

.card-img-overlay h3 {
  color: var(--primary-colour);
}

.card-img-overlay p{
  color:var(--secondary-colour);
}

.card-img-overlay button{
  border-radius: 0 !important;
  background-color: #839fb1 !important;
  color: #E7F3F9  !important;
  border: none !important;
}


.card-img-overlay button:hover {
  background-color: #576b77 !important;
}
.imageModal, .modal-body{
  padding: 0px !important;
}

.modal-body {
  background-color: var(--tertiary-colour);
}

.modalText {
  padding: 10px !important;
  color: var(--primary-colour);
}

.modalText p {
  color: var(--secondary-colour);
}


.modalLink {
  text-align: center;
}

.modalLink a {
  width: 120px;
  border-radius: 0 !important;
  background-color: #839fb1 !important;
  color: #E7F3F9  !important;
  border: none !important;
  margin: 2px;
}


.modalLink a:hover {
  background-color: #576b77 !important;
}

.modal-body button {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 30px;
  color: #032F70 !important;
}

.modal-body button:not(:hover) {
  opacity: 1;
  color: #032F70;
}


#projects {
  min-height: 100vh;
  padding-top: 6rem;
  padding-bottom: 3rem;
  background-color: var(--tertiary-colour);
}


.projectbox {
  padding:0px !important;
  max-width: 450px !important;
}

.project-area{
  padding-top: 4rem;
}

.animated {
  animation-fill-mode: backwards !important;
}

.form-group {
  margin-top: 0px !important;
  margin-bottom: 0px !important;
}

.form-control {
  background-color: #839fb1 !important;
  color: #E7F3F9 !important;
  font-size: 20px !important;
  border-radius: 0px !important;
  border: 1px solid #E7F3F9 !important;
}

.center-item {
  justify-content: center;
}


.contact-text {
  text-align: center;
  color: #8AC5FC;
  font-size: 20px;
  margin-bottom: 30px;
}

.contact-text span {
  color: #E7F3F9;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}


input:focus, textarea:focus {
  outline: 0 !important;
  box-shadow: none !important;
  border: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus {
  -webkit-text-fill-color: #E7F3F9;
  -webkit-box-shadow: 0 0 0px 1000px #839fb1 inset;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

.div-button {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
}


.div-button button {
  font-size: 20px !important;
  background-color: #839fb1 !important;
  color: #E7F3F9  !important;
  border: none !important;
  border-radius: 0px !important;
}

.div-button button:hover {
  background-color: #576b77 !important;
}
#contact {
  background-color: var(--secondary-colour);
  padding-top: 6rem;
  padding-bottom: 3rem;
  min-height: 100vh !important;
  /* position: relative; */
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
#footer {
  padding-top: 6em;
  background-color: var(--secondary-text) !important;
  height: 250px;
  text-align: center;
}

#footer hr {
  margin-top: 0px;
  margin-bottom: 0px;
  border-top: 3px solid var(--primary-colour) !important;
  width: 300px;
}
.scroll-top {
  height: 0px;
  float: right;
}

.scroll-top span {
  position: relative;
  bottom: 35px;
  padding-left: 15px;
  padding-right: 15px;
  color: var(--tertiary-colour);
  border-radius: 10px;
  font-size: 30px;
  transition: all .2s ease-in-out;
}

.scroll-top span:hover {
  cursor: pointer;
  background-color: var(--primary-colour);
}

.scroll-text{
  font-size: 20px;
  color: var(--primary-colour);
  text-align: center;
  font-weight: 600;
}

.icon a {
  font-size: 40px;
  color: var(--primary-colour);
  margin:10px;
  transition: all .2s ease-in-out;
}

.icon:hover a {
  color: var(--tertiary-colour);
  font-size: 50px;
}

.inline {
  padding-top: 20px;
  display: flex;
  justify-content: center;
  height: 100px;
  line-height: 60px;
}

.copyright {
  font-size: 16px;
  color: var(--primary-colour);
  font-family: sans-serif; 
  text-align: center;
  padding-top: 20px;
}

.copyright span {
  color: var(--tertiary-colour);
}
.particles-canvas {
  position: absolute;
  height: 100vh;
  width: 100%;
  /* background-image: linear-gradient(to bottom, #061781, #97a8df); */
  background-image: url(/static/media/background2.878889d6.jpg);
  background-size: cover;
  z-index: -1;
}

@media(max-width: 600px) {
  .particles-canvas {
    display: none;
  }
}
.content {
  font-family: 'Yusei Magic', sans-serif;
}
:root {
  --primary-colour: #289dd7;
  --secondary-colour: #090C15;
  --tertiary-colour: #fdf9fb;
  --primary-background: #5d5d5d;
}

html, body {
  height: 100%;
}

@media(max-width: 600px) {
  body {
  background-color: #fdf9fb;
  background-color: var(--tertiary-colour);
  }
}
